//COLORS
$color-pallate-1 : #011627;
$color-pallate-2 : #FDFFFC;
$color-pallate-3 : #2EC4B6;
$color-pallate-4 : #E71D36;
$color-pallate-5 : #FF9F1C;

@mixin useFlex {
  display: flex;
  display: -ms-flexbox;
  display: -webkit-flex;
}

.app {
  @include useFlex;
  flex: 1 0 auto;
  flex-direction: column;
}

@mixin centerItem {
  align-self: center;
  height: 2.5em;
  padding: 0 0.5em;
  margin: 0 0;
  border: solid 0.1px black;
}
/*SEARCH BAR*/
.navwrapper {
  display: grid;
  grid-template-columns: 1fr 0.8fr 0.2fr 1fr;
  gap: 0;
  grid-template-rows: 3em;
  background: $color-pallate-3;
  

  .search-bar {
    grid-column: 2/3;
    @include centerItem();
    border-top-left-radius: 3px;
    border-bottom-left-radius: 3px;
  }

  .search-btn {
    grid-column: 3/4;
    @include centerItem();
    border-top-right-radius: 3px;
    border-bottom-right-radius: 3px;
    border-left: none;
    background: $color-pallate-5;
    
  }
}


input:focus {
  color: black;
  background-color:$color-pallate-5;
}

/*FILTER SECTION*/
.filter-wrapper {
  border-top: $color-pallate-1 0.0625em solid;
  border-bottom: $color-pallate-1 0.0625em solid;
}
.filter-container {
  display: flex;
  flex-flow: row nowrap;
  -ms-flex-flow: row nowrap;
  padding: 0.0625em 0.0125em;
  justify-content: space-evenly;
  background-color:$color-pallate-3;
  animation: filterbackground 2s forwards;
  //transition: height 2s;
}

.filter-options {
  /*margin: 0.2125em 2.3625em;*/
  padding: 0.0625em 0.125em;
  display: flex;
  flex-flow: column nowrap;
  -ms-flex-flow: column nowrap;
  overflow: hidden;
}

.option-element {
  animation: filterelements 1s;
  flex-shrink: 2;
}

.checkbox-list {
  overflow-y:scroll;
}

.filter-btn {
  border: none;
  padding: 0 0;
  margin: 0 0;
  background-color:$color-pallate-3;
}

.filter-text {
  background-color:$color-pallate-3;
  border: 0.0125em solid $color-pallate-2;
}

.filter-selector {
  background-color: $color-pallate-5;
  padding: 0.2em;
}

.filter-btn:focus {
  outline: none;
}

.header {
  color: white;
  
  padding: 0.1em 0;
  text-align: center;
}
/*NEWS CARD*/
.grid-container {
  display: grid;
  grid-template-columns: auto auto auto auto;
  grid-gap: 1.2em;
  margin: 1.2em 1.2em;
}
.grid-container > div {
  text-align: center;
  padding: 1.2em 0;
  border: 0.125em solid $color-pallate-1;
  background:$color-pallate-2;
  border-radius: 0.3125em;
}
.article-box {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  padding: 0em;
  margin: 0em;
}
.article-title {
  flex-grow: 0.5;
  padding-left: 1.2em;
  padding-right: 1.2em;
  font-size: 1.2em;
  font-weight: bold;
}
.article-img {
  flex-grow: 3;
  margin-top: 1.4em;
}
.article-snippet {
  flex-grow: 6;
  margin: 0.5em 1.2em;
}
.article-date {
  flex-grow: 1;
  font-size: 0.8em;
  border: #282828 0.0625em solid;
  border-radius: 0.3125em;
  background-color: $color-pallate-3;
  color: $color-pallate-1;
  padding: 0.125em;
  align-self: center;
}
img {
  width: 90%;
  height: auto;
  object-fit: scale-down; 
}

/*PAGINATION*/
.page-list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0em;
  padding: 0em;
}

.page-item {
  list-style-type: none;
  margin: 0.2em;
  background-color:$color-pallate-3;
  border: none;
}

.page-item::selection {
  background-color: $color-pallate-2;
}

a.page-link {
  text-decoration: none;
  color: #282828;
}

/*SCROLLBAR*/
::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 2px $color-pallate-3;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  background: $color-pallate-2;
  border-radius: 5px;
}

::-webkit-scrollbar-thumb:hover {
  background: $color-pallate-2;
}

/*FOOTER*/
.footer {
  flex-shrink: 0;
  background-color: $color-pallate-3;
  height: 3.5em;
}

.footer > div {
  margin: 0.625em 1.25em;
}

@keyframes filterbackground {
  from{
    height: 0em;
    background-color: $color-pallate-1;
  }
  to{
    height: 12.4013em;
    background-color: $color-pallate-3;
  }
}

@keyframes filterelements {
  from{
    opacity: 0%;
  }
  to{
    opacity: 100%; 
  }
}

@media only screen and (min-width: 1400px) {
  .grid-container {
    display: grid;
    grid-template-columns: auto auto auto auto auto;
    grid-gap: 1.2em;
    margin: 1.2em 1.2em;
  }
}

@media only screen and (max-width: 960px) {
  .grid-container {
    display: grid;
    grid-template-columns: auto auto;
    grid-gap: 1.2em;
    margin: 1.2em 1.2em;
  }
}

@media only screen and (max-width: 500px) {
  .grid-container {
    display: grid;
    grid-template-columns: auto;
    grid-gap: 1.2em;
    margin: 1.2em 1.2em;
  }

}